body{
    padding: 0;
    margin: 0;
    font-family: 'Roboto', Arial, sans-serif;
    font-size: 14px;
    line-height: 23px;
    color: #959595;
    background-color: #fff;
}

/* ==========================================================================
    1. TYPOGRAPHY
    ========================================================================= */
p,
a,
span {
    color: #959595;
    font-family: 'Roboto', Arial, sans-serif;
    line-height: 23px;
}
p {
    padding-bottom: 15px;
}
a {
    -webkit-transition: all 0.2s ease 0s;
    -moz-transition: all 0.2s ease 0s;
    -o-transition: all 0.2s ease 0s;
    -ms-transition: all 0.2s ease 0s;
    transition: all 0.2s ease 0s;
    text-decoration: none;
}
a:hover {
    text-decoration: none;
}
a.learn-more {
    position: relative;
    text-align: left;
    display: block;
    width: 100%;
    padding-left: 90px;
    margin-top: 10px;
}
h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: 'Roboto', 'Roboto', Arial, sans-serif;
    font-weight: 300;
    color: #363636;
    margin-top: 0;
    margin-bottom: 15px;
}
h2 {
    font-size: 31px;
    line-height: 31px;
}
h2 {
    font-size: 28px;
    line-height: 28px;
}
h3 {
    font-size: 24px;
    line-height: 28px;
    color: #959595;
}
h4 {
    font-size: 21px;
    line-height: 28px;
    color: #959595;
}
h5 {
    font-size: 18px;
    line-height: 24px;
}
h6 {
    font-size: 15px;
    line-height: 15px;
}
strong {
    font-weight: 600;
}
strong a:hover {
    text-decoration: underline;
}
img {
    max-width: 100%;
    height: auto;
    display: block;
}
img.float-left {
    float: left;
    margin: 12px 12px 12px 0;
}
img.float-right {
    float: right;
    margin: 12px 0px 12px 12px;
}
.img-full-size {
    width: 100%;
    height: auto;
}
.img-center {
    display: table;
    margin: 0 auto;
}
ul,
ol {
    list-style-position: inside;
    list-style-type: none;
}
ul li,
ol li {
    padding-bottom: 0;
}
.listy-style-none {
    list-style: none;
}
ul.list-border li,
ol.list-border li {
    border-bottom: 1px dotted #ccc;
    padding-bottom: 8px;
    padding-top: 3px;
}
ul.list-border li:last-child,
ol.list-border li:last-child {
    border-bottom: none;
    padding-bottom: 0;
}
ul.list-border li:first-child,
ol.list-border li:first-child {
    padding-top: 0;
}
ul.list-animated li,
ol.list-animated li {
    cursor: pointer;
    -webkit-transition: all 0.2s ease 0s;
    -moz-transition: all 0.2s ease 0s;
    -o-transition: all 0.2s ease 0s;
    -ms-transition: all 0.2s ease 0s;
    transition: all 0.2s ease 0s;
}
ul.list-animated li:hover,
ol.list-animated li:hover {
    text-indent: 5px;
}
blockquote + p {
    margin-top: 15px;
}
.custom-background.dark h1,
.custom-background.dark h2 {
    margin-bottom: 10px;
}
.list-margin {
    margin: 40px 20px;
}
.page-content .col-md-12 .subtitle h3,
.page-content .col-md-12 .subtitle h4{
    padding: 0 15%;
}
.page-content .container-fluid .col-md-12 .subtitle h3,
.page-content .container-fluid .col-md-12 .subtitle h4 {
    padding: 0 28%;
}
.page-content.custom-img-background .col-md-12 .subtitle h3,
.page-content.custom-img-background .col-md-12 .subtitle h4{
    padding: 0 12%;
    font-weight: 100;
}
.page-content.custom-img-background.dark .col-md-12 .subtitle h3,
.page-content.custom-img-background.dark .col-md-12 .subtitle h4 {
    color: #d2d2d2;
}
.page-content.full-screen-background .subtitle h5{
    padding: 0 32%;
    margin-top: 40px;
}
